<template>
    <div class="home">
        <Banner home title="我的任务"></Banner>
        <ul id="tasks">
            <li class="actived">我的任务</li>
            <li>我发起的任务</li>
        </ul>
        <Task ref="mTasks" class="tasks"></Task>
    </div>
</template>
<script>
import Banner from './items/Banner'
import Task from './items/Task'
export default {
    components: {
        Banner,
        Task
    },
    data(){
        return {
            flag: 0
        }
    },
    methods: {
        handleTask(ref) {
            let oUl = document.getElementById('tasks');
            if(oUl){
                oUl.addEventListener('click', function(ev){
                    let e = ev || window.event;
                    let target = e.target || e.srcElement;
                    if(target && target.nodeName=='LI'){
                        console.log(this);
                        let child = this.children;
                        for(let i=0;i<child.length;i++){
                            if(child[i]==target){
                                child[i].className = 'actived';
                                this.flag = i;
                                ref['toggleData'](i);
                            }else{
                                child[i].className = '';
                            }
                        }
                    }
                })
            }
        }
    },
    beforeCreate() {
        console.log('Mine.vue beforeCreate');
    },
    created() {
        console.log('Mine.vue Created');
    },
    beforeMount() {
        console.log('Mine.vue beforeMount');
    },
    mounted() {
        console.log('Mine.vue mounted');
        console.log(this.$route);
        this.handleTask(this.$refs.mTasks);
    },
    beforeUpdate() {
        console.log('Mine.vue beforeUpdate');
    },
    updated() {
        console.log('Mine.vue updated');
    },
    beforeDestroy() {
        console.log('Mine.vue beforeDestroy');
    },
    destroyed() {
        console.log('Mine.vue destroyed');
    },
}
</script>
