<template>
    <div>
        <div class="taskItem" v-if="taskItem" v-for="(it,index) in tasks"
            @click="handleTaskClick(taskItem.pId,taskItem.mId,it.tId)">
            <span :class="{'choosed':it.done, 'noChoose':!it.done, 'icon':true}"></span>
            <p>{{it.tName}}</p>
        </div>
    </div>
</template>
<script>
export default {
    props: {
        taskItem: {
            type: Object,
            default: {}
        }
    },
    data(){
        return {
            tasks: this.taskItem.tasks
        }
    },
    methods: {
        handleTaskClick(pId,mId,tId){
            this.$router.push({
                name: 'taskDetail',
                query: {
                    pId: pId,
                    mId: mId,
                    tId: tId
                }
            })
        }
    },
    beforeCreate() {
        console.log('TaskItem.vue beforeCreate');
    },
    created() {
        console.log('TaskItem.vue Created');
    },
    beforeMount() {
        console.log('TaskItem.vue beforeMount');
    },
    mounted() {
        console.log('TaskItem.vue mounted');
        console.log(this.$route);
    },
    beforeUpdate() {
        console.log('TaskItem.vue beforeUpdate');
    },
    updated() {
        console.log('TaskItem.vue updated');
    },
    beforeDestroy() {
        console.log('TaskItem.vue beforeDestroy');
    },
    destroyed() {
        console.log('TaskItem.vue destroyed');
    },
}
</script>
