<template>
    <div class="task">
        <div class="undone" v-if="undone.length>0">
            <h2 @click="toggleUndoneShow"><span>{{undoneShowed}}</span>未完成的任务</h2>
            <ul v-if="undoneShow">
                <li v-for="(item,index) in undone" :key="index">
                    <TaskItem :task-item="item"></TaskItem>
                </li>
            </ul>
        </div>
        <div class="done" v-if="done.length>0">
            <h2 @click="toggleDoneShow"><span>{{doneShowed}}</span>已完成的任务</h2>
            <ul v-if="doneShow">
                <li v-for="(item,index) in done" :key="index">
                    <TaskItem :task-item="item"></TaskItem>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import TaskItem from './TaskItem'
import {mapState} from 'vuex'
export default {
    components: {
        TaskItem
    },
    data(){
        return {
            undoneShow: true,
            undoneShowed: '隐藏',
            doneShow: false,
            doneShowed: '查看',
            flag: false,
            tData: []
        }
    },
    computed: {
        ...mapState({
            taskData: 'taskData',
            mData: 'memberData',
            mId: 'ownId'
        }),
        myTaskData(){
            return this.taskData.filter(item => {
                return item.mId == this.mId;
            });
        },
        undone() {
            let len=0,res=[],tmp;
            if(this.flag){
                len = this.tData.length;
                for(var i=0;i<len;i++){
                    tmp = this.tData[i].tasks.filter(item => {
                        return !item.done;
                    });
                    res.push({
                        pId: this.tData[i].pId,
                        mId: this.tData[i].mId,
                        tasks: tmp
                    });
                }
            }else{
                len = this.taskData.length;
                for(var j=0;j<len;j++){
                    tmp = this.taskData[j].tasks.filter(item => {
                        return !item.done;
                    })
                    res.push({
                        pId: this.taskData[j].pId,
                        mId: this.taskData[j].mId,
                        tasks: tmp
                    });
                }
            }
            return res;
        },
        done(){
            let len=0,res=[],tmp;
            if(this.flag){
                len = this.tData.length;
                for(var i=0;i<len;i++){
                    tmp = this.tData[i].tasks.filter(item => {
                        return item.done;
                    });
                    res.push({
                        pId: this.tData[i].pId,
                        mId: this.tData[i].mId,
                        tasks: tmp
                    });
                }
            }else{
                len = this.taskData.length;
                for(var j=0;j<len;j++){
                    tmp = this.taskData[j].tasks.filter(item => {
                        return item.done;
                    })
                    res.push({
                        pId: this.taskData[j].pId,
                        mId: this.taskData[j].mId,
                        tasks: tmp
                    });
                }
            }
            return res;
        }
    },
    watch: {
        'doneShow'(){
            this.doneShowed = this.show ? '隐藏' : '查看';
        },
        'undoneShow'(){
            this.undoneShowed = this.undoneShow ? '隐藏' : '查看';
        }
    },
    methods: {
        toggleUndoneShow(){
            this.undoneShow = !this.undoneShow;
        },
        toggleDoneShow(){
            this.doneShow = !this.doneShow;
        },
        toggleData(val=0){
            this.flag = true;
            if(val == 0){
                console.log('taskData toggleData', this.taskData);
                this.tData = this.taskData;
            }else{
                console.log('myTaskData toggleData', this.myTaskData);
                this.tData = this.myTaskData;
            }
        }
    },
    beforeCreate() {
        console.log('Task.vue beforeCreate');
    },
    created() {
        console.log('Task.vue Created');
    },
    beforeMount() {
        console.log('Task.vue beforeMount');
    },
    mounted() {
        console.log('Task.vue mounted');
        console.log(this.$route);
    },
    beforeUpdate() {
        console.log('Task.vue beforeUpdate');
    },
    updated() {
        console.log('Task.vue updated');
    },
    beforeDestroy() {
        console.log('Task.vue beforeDestroy');
    },
    destroyed() {
        console.log('Task.vue destroyed');
    },
}
</script>
